<template>
	<div id="statcenter-takeout">
		<public-header title="外卖单统计"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group">
					<div class="tab-item" :class="{active: days == 0}" @click="onToggleTab(0)">今天</div>
					<div class="tab-item" :class="{active: days == 7}" @click="onToggleTab(7)">近7天</div>
					<div class="tab-item" :class="{active: days == 30}" @click="onToggleTab(30)">近30天</div>
					<div class="tab-item" :class="{active: days == -1}" @click="onToggleTab(-1)">自定义</div>
				</div>
			</div>
			<van-list class="border-0px">
				<div class="diy-time" v-if="days == -1">
					<span @click="onTogglePicker('start')">
						<input v-model="stat_day.start" type="text"  disabled>
					</span>
					<span class="padding-15-lr c-gray">~</span>
					<span @click="onTogglePicker('end')">
						<input v-model="stat_day.end" type="text" disabled>
					</span>
					<van-button size="mini" class="bg-primary margin-10-l" :disabled="!stat_day.start || !stat_day.end" @click="onLoad">确定</van-button>
				</div>
				<van-cell-group class="border-0px">
					<van-cell title="总收入" :value="`¥${stat.takeout.total_fee}`"></van-cell>
					<van-cell title="配送成功" :value="stat.takeout.success_order"></van-cell>
					<van-cell title="货到付款金额" :value="`¥${stat.takeout.total_paytype_delivery}`" v-if="0"></van-cell>
				</van-cell-group>
			</van-list>
			<div class="padding-15" v-if="days != 0">
				<router-link :to="util.getUrl({path: '/pages/statcenter/detail', query: {days: days, start: stat_day.start, end: stat_day.end, type: 'takeout'}})" tag="button" class="van-button van-button van-button--default van-button--normal van-button--block bg-primary font-16">查看详情</router-link>
			</div>
		</div>
		<van-popup v-model="timePicker" position="bottom">
			<van-datetime-picker
					v-model="currentDate"
					type="date"
					:min-date="minDate"
					:max-date="maxDate"
					@cancel="onTogglePicker"
					@confirm="onConfirmTime"
			/>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				timePicker: false,
				minDate: new Date(2014, 12, 1),
				maxDate: new Date(),
				currentDate: new Date(),
				showPreLoading: true,

				stat: {
					takeout: {}
				},
				days: 0,
				timeType: '',
				stat_day: {
					start: this.util.formatDate( new Date(), 'date'),
					end: this.util.formatDate( new Date(), 'date')
				}
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/statcenter/stat/stat_vue',
					data: {
						type: 'takeout',
						days: that.days,
						stat_day: that.stat_day
					},
					success: function(res) {
						that.stat = res.stat;
					}
				})
			},
			onTogglePicker(type) {
				if(type) {
					this.timeType = type;
				}
				this.timePicker = !this.timePicker;
			},
			onConfirmTime(value) {
				value = this.util.formatDate(value, 'date');
				this.stat_day[this.timeType] = value;
				this.onTogglePicker();
			},
			onToggleTab(value) {
				if(value == this.days) {
					return;
				}
				this.days = value;
				if(this.days != -1) {
					this.onLoad();
				}
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#statcenter-takeout .van-cell .van-cell__value span{
		color: #7a7a7a;
	}
	#statcenter-takeout .diy-time{
		padding: 10px 15px;
		margin-bottom: 10px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		background-color: #fff;
	}
	#statcenter-takeout .diy-time input{
		width: 100px;
		font-size: 14px;
		border: 1px solid #ccc;
		border-radius: 3px;
		background-color: #fff;
		text-align: center;
		padding: 3px 0;
	}
	#statcenter-takeout .diy-time .van-button--mini{
		height: 26px;
	}
</style>


